@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    background-color: #EEEEEE;
    position: relative;
}

.img-head {
    background-image: url('./assets/img/keyboard-2.jpg');
}

.img-head::before {
    content: '';
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); 
    z-index: 0;
    max-width: 100%;
    box-sizing: border-box;
}

.prev {
    @apply absolute -translate-y-2/4 z-[100] max-w-full box-border bg-transparent w-[50px] border-[none] top-2/4;
  }

  .next {
    @apply absolute -translate-y-2/4 z-[100] max-w-full box-border bg-transparent w-[50px] border-[none] right-[0%] top-2/4;
  }